.statistics {
  width: 250px;
  margin: 0 auto;
  padding: 50px 20px;
}

.title {
  text-transform: uppercase;
  text-align: center;
  color: #192431;
  margin: 0 auto;
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: white;
}

.stat-list {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    padding: 0;
}

.stat-item {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 0;
    text-align: center;
  
    width: 50px;
}

.label {
    padding-top: 5px;
  }
  
  .percentage {
    font-size: 20px;
    padding-bottom: 5px;
  }