.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 60px;
  padding-bottom: 60px;
  }
  
  .profile-picture {
  display: block;
  width: 200px;
  margin-top: 20px;
  border-radius: 50%;
  background-color: gray;
  }
  
  .name {
   font-size: 22px;
  line-height: 1.62;
  font-weight: 20px;
  color: #0f0f4f;
  margin-top: 20px;
  }
  
  .description{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    width: 350px;
  }

  .userInfo{
    margin-top: 10px;
    font-size: 16px;
    line-height: 1.58;
    font-weight: 18px;
  }
  
   
  .stats {
    background-color: #fcfcfc;
    width: 350px;
    display: flex;
    margin: 0 auto;
    padding: 0;
    align-items: center;
    justify-content: center;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    list-style: none;
  }
  
  .item {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px;
    border-top: 1px solid #0f0f4f;
    border-right: 1px solid #0f0f4f;
  }
  
  .item:last-child {
    border-right: none;
  }
  
  .label {
    display: flex;
    line-height: 1.62;
    font-weight: 16px;
    text-align: center;
    justify-content: center;
  }
  
  .quantity {
    text-align: center;
    line-height: 1.58;
    font-weight: bold;
    margin: 10px auto;
  }